import React from 'react';
import { useForm } from 'react-hook-form';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline } from 'summer';
import { DestinatarioEmail } from '../../../models/dtos/ger/destinatarioEmail';

const emptyItem: DestinatarioEmail = {
  contato: '',
  email: '',
};

const DestinatarioEmailDetailEditView: React.FC<DetailEditViewFormProps<DestinatarioEmail>> = props => {
  const { errors, getValues, register, setValue, triggerValidation } = useForm<DestinatarioEmail>({
    defaultValues: props.selectedItem || emptyItem,
    mode: 'onBlur',
  });

  const formProps = {
    errors,
    getValues,
    register,
    setValue,
    triggerValidation,
  };

  const validateExtras = (currentItem: any, currentValues: any[], parentObject: any) => {
    return [];
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <InputGroupInline
        id="contato"
        isPrependLabel={true}
        formProps={{
          ...formProps,
          validation: {
            required: true,
          },
        }}
        label="Contato"
        placeholder="Informe o nome do contato"
      />

      <InputGroupInline
        id="email"
        isPrependLabel={true}
        formProps={{
          ...formProps,
          validation: {
            required: true,
          },
        }}
        label="E-mail"
        placeholder="Informe o e-mail do contato"
      />
    </DetailEditForm>
  );
};

export default DestinatarioEmailDetailEditView;
