import React, { FC } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Form } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  dispatchClose,
  ErroList,
  ERROR,
  INFORMATION,
  ProcessBar,
  showToast,
  UseFormProps,
} from 'summer';
import { ReportBasicValues } from '../../../models/dtos/ger/reportBasicValues';
import { ReportViewOptions } from '../../../models/dtos/ger/reportViewOptions';
import { gerarReport } from '../../../services/report.service';
import { Reducers } from '../../../store/ducks';
import { GlobalState, toggleHeaderDrawer } from '../../../store/ducks/global.duck';
import ReportInputs from './ReportInputs';

export interface BasicReportFormProps {
  /** Função do submit (vide useForm) */
  handleSubmit: (onSubmit: any) => any;
  /** Função de fechar/redirecionar a tela */
  handleClose?: () => void;
  /** Propriedades de formulário da tela pai (ReportView) */
  formProps: UseFormProps;
  /** Valores default da geração */
  reportBasicValues: ReportBasicValues;
  /** Configuração da tela de relatório */
  reportViewOptions: ReportViewOptions;
  /** chave que guarda as configurações de geração no cache */
  localStorageKey?: string;
  /** ID da view da tela pai */
  viewId?: string;
  /** Função de validações extras */
  validateExtras?: (formData: any) => string[];
  /** Função de envio de parametros extras */
  addExtraParameters?: (formData: any) => any;
  /** já iniciar as opções extras do relatório expandidas? */
  extraConfigOpened?: boolean;
  /** Função executada após o submit */
  afterSubmit?: () => void;
}

export interface ReportFormProps extends BasicReportFormProps {
  /** Propriedades das rotas */
  routeProps?: RouteComponentProps;
}

const ReportForm: FC<ReportFormProps> = props => {
  const { headerDrawerToggle } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const dispatch = useDispatch();

  function handleClose() {
    if (props.handleClose) {
      props.handleClose();
    } else if (props.routeProps) {
      dispatchClose(props.routeProps, props.routeProps.history);
    }
  }

  const onSubmit = (data: any, event: any) => {
    event.preventDefault();
    let erros = [];
    if (props.validateExtras) {
      erros = props.validateExtras(data);
    }
    if (erros && erros.length) {
      const params: AlertProps = {
        message: <ErroList erros={erros} />,
        type: AlertType.ERROR,
      };
      alertManager.emit(ACTION_EVENTS.SHOW, params);
    } else {
      showToast('Seu arquivo está sendo gerado. Uma notificação será exibida quando estiver finalizado e o mesmo poderá ser salvo.', INFORMATION);
      finishSubmit(data);
    }
  };

  const finishSubmit = (data: any) => {
    if (props.addExtraParameters) {
      data = props.addExtraParameters(data);
    }
    data.saveSession = false;

    gerarReport(data, props.reportViewOptions.reportEventType, {
      errorFunction: (mensagem: string) => showToast(mensagem, ERROR),
      thenFunction: () => {
        if (!headerDrawerToggle) {
          dispatch(toggleHeaderDrawer());
        }
        if (props.afterSubmit) {
          props.afterSubmit();
        }
      },
    });
  };

  return (
    <Form noValidate={true}>
      {props.children}
      <ReportInputs
        reportBasicValues={props.reportBasicValues}
        reportViewOptions={props.reportViewOptions}
        formProps={props.formProps}
        extraConfigOpened={props.extraConfigOpened}
      />

      <ProcessBar handleSubmit={props.handleSubmit(onSubmit)} handleClose={handleClose} />
    </Form>
  );
};

export default ReportForm;
