import cx from 'clsx';
import React, { FC, MouseEvent, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Col, CustomInput, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap';
import {
  Accordion,
  AccordionItem,
  ANO,
  currentMomentDate,
  DATE,
  DATES,
  DESIGN_TYPE_ENUM,
  format,
  formatDate,
  GRAPHIC,
  InputGroupInline,
  InputType,
  REF_PATTERN,
  REFERENCE,
  REFERENCES,
  UseFormProps,
} from 'summer';
import { ReportBasicValues } from '../../../models/dtos/ger/reportBasicValues';
import { ReportViewOptions } from '../../../models/dtos/ger/reportViewOptions';
import SALDO_REPORT_ENUM from '../../../models/enumerated/ger/saldoReportEnum';
import { Reducers } from '../../../store/ducks';
import { GlobalState } from '../../../store/ducks/global.duck';
import EnvioEmail from '../envioEmail/EnvioEmail';
import EmpresaIS from '../ger/empresaIS/EmpresaIS';

export interface ReportProps {
  /** Valores default da geração */
  reportBasicValues: ReportBasicValues;
  /** Opções do relatório */
  reportViewOptions: ReportViewOptions;
  /** UseFormProps (vide useForm) */
  formProps: UseFormProps;
  /** já iniciar as opções extras do relatório expandidas? */
  extraConfigOpened?: boolean;
}

const ReportInputs: FC<ReportProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const [activeTab, setActiveTab] = useState(1);

  useEffect(
    () => {
      const { printingTypes, reportViewType } = props.reportViewOptions;
      let resetObject: any = {
        ...props.reportBasicValues,
        designType: printingTypes && printingTypes.length > 0 ? printingTypes[0] : GRAPHIC.value,
        idGerEmpresa: globalReducer.globalParameter.empresa.id,
        startPage: 1,
      };
      const current = currentMomentDate();
      const endDate = currentMomentDate();
      switch (reportViewType) {
        case DATE.value:
          resetObject = {
            ...resetObject,
            dtInicial: formatDate(current.toDate()),
          };
          break;
        case DATES.value:
          resetObject = {
            ...resetObject,
            dtFinal: formatDate(endDate.endOf('month').toDate()),
            dtInicial: formatDate(current.toDate()),
          };
          break;
        case REFERENCE.value:
          resetObject = {
            ...resetObject,
            referenciaInicial: format(current.toDate(), REF_PATTERN),
          };
          break;
        case REFERENCES.value:
          resetObject = {
            ...resetObject,
            referenciaFinal: format(current.toDate(), REF_PATTERN),
            referenciaInicial: format(endDate.toDate(), REF_PATTERN),
          };
          break;
        case ANO.value:
          resetObject = {
            ...resetObject,
            ano: String(current.year()),
          };
          break;
        default:
          break;
      }
      props.formProps.reset(resetObject);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [props.reportViewOptions, globalReducer.globalParameter.empresa]
  );

  const toggleTab = ({ target }: MouseEvent) => {
    const { tabIndex } = target as HTMLElement;
    if (activeTab !== tabIndex) {
      setActiveTab(tabIndex);
    }
  };

  const restoreTitle = () => {
    props.formProps.setValue('title', props.reportBasicValues.title, true);
  };

  const dateInitial = (
    <InputGroupInline
      id="dtInicial"
      label={props.reportViewOptions.dtInicialLabel}
      isPrependLabel={true}
      formProps={{ ...props.formProps, validation: { required: props.reportViewOptions.dtInicialRequired } }}
      type={InputType.DATE}
    />
  );

  const dateComponent = props.reportViewOptions.reportViewType === DATE.value ? dateInitial : null;

  const datesComponent =
    props.reportViewOptions.reportViewType === DATES.value ? (
      <>
        {dateInitial}

        <InputGroupInline
          id="dtFinal"
          label={props.reportViewOptions.dtFinalLabel}
          isPrependLabel={true}
          formProps={{ ...props.formProps, validation: { required: props.reportViewOptions.dtFinalRequired } }}
          type={InputType.DATE}
        />
      </>
    ) : null;

  const referenceInitial = (
    <InputGroupInline
      id="referenciaInicial"
      label={props.reportViewOptions.refInicialLabel}
      isPrependLabel={true}
      formProps={{ ...props.formProps, validation: { required: props.reportViewOptions.refInicialRequired } }}
      type={InputType.REFERENCE}
    />
  );

  const referenceComponent = props.reportViewOptions.reportViewType === REFERENCE.value ? referenceInitial : null;

  const referencesComponent =
    props.reportViewOptions.reportViewType === REFERENCES.value ? (
      <>
        {referenceInitial}

        <InputGroupInline
          id="referenciaFinal"
          label={props.reportViewOptions.refFinalLabel}
          isPrependLabel={true}
          formProps={{ ...props.formProps, validation: { required: props.reportViewOptions.refFinalRequired } }}
          type={InputType.REFERENCE}
        />
      </>
    ) : null;

  const anoComponent =
    props.reportViewOptions.reportViewType === ANO.value ? (
      <InputGroupInline
        id="ano"
        label={props.reportViewOptions.anoLabel}
        isPrependLabel={true}
        formProps={{ ...props.formProps, validation: { required: props.reportViewOptions.anoRequired } }}
        inputMaskedProps={{ mask: '9999' }}
        type={InputType.MASK}
      />
    ) : null;

  const startPageComponent = props.reportViewOptions.exibirPaginaInicial ? (
    <InputGroupInline
      id="startPage"
      label="Página Inicial"
      isPrependLabel={true}
      placeholder="Página Inicial"
      formProps={{
        ...props.formProps,
        validation: {
          min: 0,
          required: true,
        },
      }}
      type={InputType.NUMBER}
    />
  ) : null;

  const multiplasEmpresasComponent = props.reportViewOptions.exibirConfiguracaoMultiplasEmpresas ? (
    <>
      <Row className="align-items-center">
        <Col xs={2}>
          <CustomInput type="switch" id="gerarPorEmpresas" name="gerarPorEmpresas" label="Gerar por Empresas" innerRef={props.formProps.register} />
        </Col>
        <Col>
          <EmpresaIS
            formProps={props.formProps}
            label="Empresa(s)"
            placeholder="Selecione uma ou várias empresas"
            id="empresasSelecionadas"
            isPrependLabel={true}
            inputProps={{ disabled: props.formProps.getValues().gerarPorEmpresas, style: { marginBottom: 20 } }}
            inputSearchProps={{ size: 'xl', selectProps: { isMulti: true } }}
          />
        </Col>
      </Row>
    </>
  ) : null;

  const segmentacaoFolhaComponent = props.reportViewOptions.exibirSegmentacaoFolha ? (
    <Row className="mb-3">
      <Col>
        <CustomInput
          type="switch"
          id="segmentacaoFolha"
          name="segmentacaoFolha"
          label="Exibir Número das Páginas como Folhas"
          innerRef={props.formProps.register}
        />
      </Col>
    </Row>
  ) : null;

  const dataAtualComponent = props.reportViewOptions.exibirConfiguracaoDataAtual ? (
    <Row className="mb-3">
      <Col>
        <CustomInput type="switch" id="showCurrentDate" name="showCurrentDate" label="Imprimir a Data Atual" innerRef={props.formProps.register} />
      </Col>
    </Row>
  ) : null;

  const logoAdmComponent = props.reportViewOptions.exibirConfiguracaoLogoAdministradora ? (
    <Row className="mb-3">
      <Col>
        <CustomInput
          type="switch"
          id="showLogoAdministradora"
          name="showLogoAdministradora"
          label="Imprimir o Logo Administradora"
          innerRef={props.formProps.register}
        />
      </Col>
    </Row>
  ) : null;

  const dadosEmpresaComponent = props.reportViewOptions.exibirConfiguracaoDadosEmpresa ? (
    <>
      <Row className="mb-3">
        <Col>
          <CustomInput
            type="switch"
            id="showDadosEmpresa"
            name="showDadosEmpresa"
            label="Imprimir os Dados da Empresa"
            innerRef={props.formProps.register}
          />
        </Col>
      </Row>

      <Row className="mb-3">
        <Col>
          <CustomInput
            type="switch"
            id="imprimirCodigoEmpresa"
            name="imprimirCodigoEmpresa"
            label="Imprimir o Código Empresa"
            innerRef={props.formProps.register}
          />
        </Col>
      </Row>
    </>
  ) : null;

  const formatacaoValoresComponent = props.reportBasicValues.exibirFormatacaoValor ? (
    <>
      {props.reportViewOptions.exibirImprimirValoresColoridos && (
        <Row className="mb-3">
          <Col>
            <CustomInput
              type="switch"
              id="imprimirValoresColoridos"
              name="imprimirValoresColoridos"
              label="Imprimir Valores Devedores em Azul e Credores em Vermelho"
              innerRef={props.formProps.register}
            />
          </Col>
        </Row>
      )}

      <InputGroupInline
        id="saldoReportEnum"
        label="Tipo de Formatação de Valores"
        isPrependLabel={true}
        placeholder="Selecione o tipo de formatação dos valores"
        formProps={{ ...props.formProps, validation: { required: true } }}
        inputDropDownProps={{
          optionsProvider: SALDO_REPORT_ENUM,
          selectProps: { isClearable: false },
        }}
        type={InputType.DROP_DOWN}
      />
    </>
  ) : null;

  return (
    <Accordion formProps={props.formProps} firstOpen={props.extraConfigOpened || false}>
      <AccordionItem title="Configurações adicionais do Relatório">
        <>
          <Nav tabs={true}>
            <NavItem>
              <NavLink className={cx({ active: activeTab === 1 })} tabIndex={1} onClick={toggleTab}>
                Configurações Gerais
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                className={cx({ active: activeTab === 2 })}
                tabIndex={2}
                onClick={toggleTab}
                disabled={!props.reportViewOptions.exibirEnviarPorEmail}
              >
                Envio de E-mail
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className={cx({ active: activeTab === 3 })} tabIndex={3} onClick={toggleTab} disabled={true}>
                Connect
              </NavLink>
            </NavItem>
          </Nav>

          <TabContent className="mb-5" activeTab={activeTab}>
            <TabPane tabId={1} className="mb-0 p-3">
              <InputGroupInline formProps={props.formProps} id="idGerEmpresa" hidden={true} />
              <InputGroupInline
                id="title"
                label="Título"
                isPrependLabel={true}
                placeholder="Título"
                iconAppend="undo-alt"
                appendClickHandler={restoreTitle}
                formProps={{
                  ...props.formProps,
                  validation: {
                    maxLength: 120,
                    minLength: 5,
                    required: true,
                  },
                }}
              />

              {startPageComponent}

              {multiplasEmpresasComponent}

              {segmentacaoFolhaComponent}

              {dataAtualComponent}

              {formatacaoValoresComponent}

              {logoAdmComponent}

              {dadosEmpresaComponent}

              {dateComponent}
              {datesComponent}
              {referenceComponent}
              {referencesComponent}
              {anoComponent}

              <hr />

              <Row className="mb-3">
                <Col>
                  <CustomInput
                    type="switch"
                    id="gerarCabecalhoPadrao"
                    name="gerarCabecalhoPadrao"
                    label="Exibir Cabeçalho"
                    innerRef={props.formProps.register}
                  />
                </Col>
              </Row>

              <Row className="mb-3">
                <Col>
                  <CustomInput
                    type="switch"
                    id="virtualization"
                    name="virtualization"
                    label="Virtualizar Relatório"
                    innerRef={props.formProps.register}
                  />
                </Col>
              </Row>

              <InputGroupInline
                id="designType"
                label="Tipo de Impressão"
                isPrependLabel={true}
                placeholder="Selecione o tipo de impressão"
                formProps={{ ...props.formProps, validation: { required: true } }}
                inputDropDownProps={{
                  optionsProvider: props.reportViewOptions.printingTypes ? props.reportViewOptions.printingTypes : DESIGN_TYPE_ENUM,
                  selectProps: { isClearable: false },
                }}
                type={InputType.DROP_DOWN}
              />

              <Row className="mb-3">
                <Col>
                  <CustomInput
                    type="switch"
                    id="enviarPorEmail"
                    name="enviarPorEmail"
                    label="Enviar por E-mail"
                    innerRef={props.formProps.register}
                  />
                </Col>
              </Row>

              <Row className="mb-3">
                <Col>
                  <CustomInput
                    type="switch"
                    id="enviarParaConnect"
                    name="enviarParaConnect"
                    label="Enviar para o Connect"
                    innerRef={props.formProps.register}
                  />
                </Col>
              </Row>
            </TabPane>
            <TabPane tabId={2} className="mb-0 p-3">
              <EnvioEmail formProps={props.formProps} initialSubject={props.reportBasicValues.title} />
            </TabPane>
            <TabPane tabId={3} className="mb-0 p-3">
              {/* TODO fazer tela de envio de mensagens do Connect*/}
            </TabPane>
          </TabContent>
        </>
      </AccordionItem>
    </Accordion>
  );
};

export default ReportInputs;
