import { BasicReportViewOptions } from 'summer';

export interface ReportViewOptions extends BasicReportViewOptions {
  exibirEnviarPorEmail?: boolean;

  exibirPaginaInicial?: boolean;
  exibirConfiguracaoMultiplasEmpresas?: boolean;
  exibirSegmentacaoFolha?: boolean;
  exibirConfiguracaoDataAtual?: boolean;
  exibirImprimirValoresColoridos?: boolean;
  exibirConfiguracaoLogoAdministradora?: boolean;
  exibirConfiguracaoDadosEmpresa?: boolean;
}

export function getDefaultReportViewOptions(reportEventType: string, reportViewType: string = null) {
  return {
    anoLabel: 'Ano',
    dtFinalLabel: 'Data Final',
    dtInicialLabel: 'Data Inicial',
    exibirConfiguracaoDadosEmpresa: true,
    exibirConfiguracaoDataAtual: true,
    exibirConfiguracaoLogoAdministradora: true,
    exibirEnviarPorEmail: true,
    exibirPaginaInicial: true,
    refFinalLabel: 'Referência Final',
    refInicialLabel: 'Referência Inicial',
    reportEventType,
    reportViewType,
  };
}
