import React, { FC } from 'react';
import { Col, CustomInput, Row } from 'reactstrap';
import { ColumnsProps, DetailManagerForm, InputGroupInline, InputType, UseFormProps } from 'summer';
import DestinatarioEmailDetailEditView from './DestinatarioEmailDetailEditView';

export interface EnvioEmailProps {
  /** UseFormProps (vide useForm) */
  formProps: UseFormProps;
  /** Assunto padrão */
  initialSubject?: string;
}

const EnvioEmail: FC<EnvioEmailProps> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'contato',
      sort: true,
      text: 'Contato',
    },
    {
      dataField: 'email',
      sort: true,
      text: 'E-mail',
    },
  ];

  return (
    <>
      <p>Dados para Envio do E-mail</p>
      <InputGroupInline
        id="envioEmail.assunto"
        label="Assunto"
        isPrependLabel={true}
        placeholder="Assunto"
        formProps={{
          ...props.formProps,
          validation: {
            required: true,
          },
        }}
      />

      <DetailManagerForm
        columns={columns}
        title="Destinatário E-mail"
        detailView={DestinatarioEmailDetailEditView}
        keyField="email"
        formProps={props.formProps}
        propName="envioEmail.destinatarios"
        {...props}
      />

      <p>Copiar para:</p>
      <Row className="mb-3">
        <Col>
          <CustomInput
            type="switch"
            id="enviarEmailAdministradora"
            name="envioEmail.enviarEmailAdministradora"
            label="Administradora"
            innerRef={props.formProps.register}
          />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            id="enviarEmailUsuarioLogado"
            name="envioEmail.enviarEmailUsuarioLogado"
            label="Usuário Logado"
            innerRef={props.formProps.register}
          />
        </Col>
      </Row>

      <InputGroupInline
        id="envioEmail.mensagem"
        label="Mensagem"
        isPrependLabel={true}
        placeholder="Mensagem"
        formProps={{
          ...props.formProps,
          validation: {
            maxLength: 1000,
          },
        }}
        type={InputType.TEXT_AREA}
      />
    </>
  );
};

export default EnvioEmail;
