import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/ger/empresaVW';

export interface EmpresaInputSearchProps extends InputProps {
  dtoClassName?: string;
}

const EmpresaIS: FC<EmpresaInputSearchProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'razaoSocial',
            text: 'Razão Social',
          },
          {
            dataField: 'nomeFantasia',
            text: 'Nome Fantasia',
          },
          {
            dataField: 'cnpj',
            text: 'CNPJ',
          },
          {
            dataField: 'cpf',
            text: 'CPF',
          },
          {
            dataField: 'cei',
            text: 'CEI',
          },
          {
            dataField: 'ie',
            text: 'IE',
          },
          {
            dataField: 'im',
            text: 'IM',
          },
          {
            dataField: 'grupo',
            text: 'Grupo',
          },
        ],
        dtoClassName: props.dtoClassName || CLASS_NAME,
        searchViewId: 'EmpresaSearchView',
      }}
      label={props.label ? props.label : 'Empresa'}
      placeholder={props.placeholder ? props.placeholder : 'Informe a Empresa'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default EmpresaIS;
