import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { InputGroupInline, InputType, parseDateToRef, toDate, WindowComponent, WindowComponentProps } from 'summer';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { CagedParam } from '../../../../../models/dtos/mfp/cagedParam';
import { ADMITIDOS_DEMITIDOS_PERIODO_ENUM_VALUES, NOME_TRABALHADOR } from '../../../../../models/enumerated/mfp/admitidosDemitidosPeriodoEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import EmpresaIS from '../../../../components/ger/empresaIS/EmpresaIS';
import ReportForm from '../../../../components/report/ReportForm';
import { AdmitidosDemitidosPeriodoRV, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from './admitidosDemitidosPeriodoRV';

const DEFAULT_VALUES: AdmitidosDemitidosPeriodoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: false,
};

const AdmitidosDemitidosPeriodoReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<AdmitidosDemitidosPeriodoRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });
  const extraFormProps = useForm<CagedParam>({ mode: 'onBlur' });
  const [isGerarPorEmpresas, setIsGerarPorEmpresas] = useState();
  const [isRelatorioDiario, setIsRelatorioDiario] = useState();

  useEffect(
    () => {
      setTimeout(() => {
        formProps.setValue('ordenacao', NOME_TRABALHADOR.value);
        formProps.setValue('gerarPorEmpresas', false);
        carregarParametrosEmpresa();
        formProps.register({ name: 'parametrosCaged' });
      }, 200);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const carregarParametrosEmpresa = () => {
    extraFormProps.register({ name: 'responsavel' });
    extraFormProps.register({ name: 'tipoResponsavel' });
    extraFormProps.register({ name: 'empresaResponsavel' });

    const { empresa, empresaMFP, usuario } = globalParameter;

    formProps.setValue('usuario', usuario);

    if (empresaMFP) {
      extraFormProps.setValue('referencia', empresaMFP.referencia);
      if (empresa.responsavelContador) {
        extraFormProps.setValue('responsavel', empresa.responsavelContador);
      }
      extraFormProps.setValue('tipoResponsavel', empresaMFP.tipoResponsavelCaged);
    }
    extraFormProps.setValue('empresaResponsavel', empresa);
  };

  const onChangeRelatorioDiario = () => {
    setIsRelatorioDiario(extraFormProps.getValues().isCagedDiario);
  };

  const onChangeGerarPorEmpresas = () => {
    setIsGerarPorEmpresas(formProps.getValues().gerarPorEmpresas);
    formProps.setValue('empresasSelecionadas', []);
  };

  const validateExtras = () => {
    const erros = [];

    const { referencia, dtCagedDiario, isCagedDiario } = extraFormProps.getValues();

    if (isCagedDiario && !dtCagedDiario) {
      erros.push('A Data de Geração do CAGED Diário não pode estar em branco.');
    }

    if (referencia && dtCagedDiario && parseDateToRef(toDate(dtCagedDiario)) !== referencia) {
      erros.push('A Data de Geração do CAGED Diário é diferente da referência informada.');
    }

    return erros;
  };

  const addExtraParameters = (formData: any) => {
    formData.parametrosCaged = extraFormProps.getValues();
    return formData;
  };

  return (
    <WindowComponent title="Funcionários Admitidos e Demitidos no Período" wikiId={3610}>
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
        addExtraParameters={addExtraParameters}
      >
        <InputGroupInline id="usuario.id" formProps={formProps} hidden={true} />
        <Row>
          <Col className="col-6">
            <InputGroupInline
              formProps={{ ...extraFormProps, validation: { required: true } }}
              id="referencia"
              label="Referência"
              placeholder="Informe a Referência"
              isPrependLabel={true}
              type={InputType.REFERENCE}
            />
          </Col>
          {isRelatorioDiario ? (
            <Col className="col-6">
              <InputGroupInline
                formProps={extraFormProps}
                id="dtCagedDiario"
                label="Data Geração CAGED Diário"
                placeholder="Informe a Data de Geração"
                isPrependLabel={true}
                type={InputType.DATE}
              />
            </Col>
          ) : null}
        </Row>
        <Row>
          <Col className="col-4">
            <CustomInput
              type="switch"
              id="gerarPorEmpresas"
              name="gerarPorEmpresas"
              label="Gerar por Empresas"
              innerRef={formProps.register}
              onChange={onChangeGerarPorEmpresas}
            />
          </Col>
          <Col className="col-8">
            <EmpresaIS
              formProps={formProps}
              label="Empresa(s)"
              placeholder="Selecione no mínimo uma empresa"
              id="empresasSelecionadas"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: true } }}
              tooltipProps={{ message: 'No mínimo uma Empresa deve ser selecionada' }}
              disabled={!isGerarPorEmpresas}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CustomInput
              type="switch"
              id="isCagedDiario"
              name="isCagedDiario"
              label="Relatório Diário (conforme CAGED Diário)"
              innerRef={extraFormProps.register}
              onChange={onChangeRelatorioDiario}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CustomInput
              type="switch"
              id="separarAdmitidosDemitidos"
              name="separarAdmitidosDemitidos"
              label="Agrupar entre Admitidos e Demitidos."
              innerRef={formProps.register}
            />
          </Col>
        </Row>
        <br />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="ordenacao"
              isPrependLabel={true}
              label="Ordem de Impressão"
              placeholder="Selecione o Tipo de Ordenação"
              inputDropDownProps={{
                optionsProvider: ADMITIDOS_DEMITIDOS_PERIODO_ENUM_VALUES,
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
      </ReportForm>
    </WindowComponent>
  );
};

export default AdmitidosDemitidosPeriodoReportView;
