import { GRAPHIC } from 'summer';
import { NATUREZA } from '../../enumerated/ger/saldoReportEnum';
import { Empresa } from './empresa';
import { EnvioEmail } from './envioEmail';
import { Usuario } from './usuario';

export interface ReportBasicValues {
  /** chave do relatório */
  key?: string;
  /** Tipo de arquivo gerado */
  designType?: string;
  /** Título do relatório */
  title?: string;
  /** Página inicial do relatório */
  startPage?: string;
  /** Data inicial para a geração do relatório */
  dtInicial?: string;
  /** Data final para a geração do relatório */
  dtFinal?: string;
  /** Referência inicial para a geração do relatório */
  referenciaInicial?: string;
  /** Referência final para a geração do relatório */
  referenciaFinal?: string;
  /** É para imprimir com a data atual ? */
  showCurrentDate?: boolean;
  empresa?: Empresa;
  saldoReportEnum?: string;
  exibirFormatacaoValor?: boolean;
  imprimirValoresColoridos?: boolean;
  /** Ano para a geração do relatório */
  ano?: string;
  showDadosEmpresa?: boolean;
  showLogoAdministradora?: boolean;

  enviarPorEmail?: boolean;
  envioEmail?: EnvioEmail;
  imprimirDataHora?: boolean;
  imprimirCodigoEmpresa?: boolean;
  usuario?: Usuario;
  segmentacaoFolha?: boolean;
  virtualization?: boolean;
  gerarBom?: boolean;
  identificadorAjuda?: string;
  gerarCabecalhoPadrao?: boolean;
  gerarCabecalhoPadraoAdministradora?: boolean;
  gerarPorEmpresas?: boolean;
  empresasSelecionadas?: Empresa[];
  identificadorMenu?: string;
  enviarParaConnect?: boolean;
  requestSolicitacao?: any; // TODO RequestSolicitacaoDTO;
  enviarEmailAdministradora?: boolean;

  /** É para salvar a sessão ? */
  saveSession?: boolean;
  idGerEmpresa?: string;
}

export function getDefaultReportBasicValues(title: string = null, identificadorAjuda: string = null, identificadorMenu: string = null) {
  return {
    designType: GRAPHIC.value,
    empresasSelecionadas: [],
    envioEmail: {
      anexos: [],
      assunto: title,
      destinatarios: [],
    },
    exibirFormatacaoValor: false,
    gerarBom: true,
    gerarCabecalhoPadrao: true,
    gerarCabecalhoPadraoAdministradora: false,
    identificadorAjuda,
    identificadorMenu,
    imprimirCodigoEmpresa: true,
    imprimirDataHora: false,
    imprimirValoresColoridos: false,
    requestSolicitacao: {},
    saldoReportEnum: NATUREZA.value,
    saveSession: false,
    showDadosEmpresa: true,
    showLogoAdministradora: true,
    startPage: '1',
    title,
    virtualization: false,
  };
}
